import React, { Component } from 'react';
import styles from './index.module.less';

export default class Right extends Component{
    _deleteForm = (formId) => {
        this.props.deleteForm(formId)
    }
    render(){
        const checkedNodes = this.props.checkedNodes || [];
        return (
            <React.Fragment>
                <div style={{fontSize:16,color:'#232425'}}>请确认已选择的表：</div>
                {
                    checkedNodes.filter(item => item.type==="form").map((item,index) => (
                        <div 
                            className={styles.rightDiv} 
                            key={index}
                        > 
                            {item.formName} 
                            <span onClick={()=>this._deleteForm(item.formId)}>X</span>
                        </div>
                    ))
                }
                <div style={{marginTop:"10px"}}>共<span style={{color:"red"}}>{checkedNodes.filter(item => item.type==="form").length}</span>张</div>
            </React.Fragment>
        )
    }
}