import React, { Component,Fragment } from 'react'
import {Tree} from 'antd'
const { TreeNode,DirectoryTree } = Tree
let totalForm = 0;
export default class Left extends Component {
    constructor(props){
        super(props);
        this.state = {
            expandedKeys:[],
            selectedKeys:[],
        }
    }
    componentDidUpdate(preProps){
        if(JSON.stringify(preProps) !== JSON.stringify(this.props)){
            this.handleTreeAutoExpand(this.props.treeData)
        }
    }
    
    renderTreeNodes = (data) => {
        return data.map((item) => {
            if(item.type==="form"){
                totalForm+=1;
            }
            if (item.children) {
                return (
                    <TreeNode title={<span>{item.name}</span>} key={item.value} isLeaf={item.type==="form"?true:false} dataRef={item}>
                        {this.renderTreeNodes(item.children)}
                    </TreeNode>
                );
            }
            return <TreeNode title={<span>{item.name}</span>} key={item.value} isLeaf={item.type==="form"?true:false} dataRef={item}/>;
        });
    }
    // 处理默认自动展开
    handleTreeAutoExpand=(data)=>{
        // 根据data拿到expandKeys
        let keys=[]
        this.callbackExpand(data,keys)
        // console.log(keys)
        this.setState({
            expandedKeys:keys
        })
    }
    callbackExpand=(data,res)=>{
        data.forEach(item=>{
            if(item.children){
                res.push(item.value)
                this.callbackExpand(item.children,res)
            }
        })
    }
    handleCheck=(checkedKeys,e)=>{
        totalForm = 0;
        const checkedNodes = e.checkedNodes.map(
            item => ({formId:item.props.dataRef.value,formName:item.props.dataRef.name,type:item.props.dataRef.type})
        )
        this.props.handleCheckTree(checkedKeys,e.halfCheckedKeys,checkedNodes)
    }
    handleExpand=(expandedKeys)=>{
        totalForm = 0;
        this.setState({
            expandedKeys
        })
    }
    render() {
        let treeData=this.props.treeData || [];
        if(treeData.length>0){
            return (
                <Fragment>
                    <div style={{fontSize:16,color:'#232425'}}>
                        请选择要填报的表：
                    </div>
                    <DirectoryTree 
                        checkable={true}
                        onCheck={this.handleCheck}
                        onExpand={this.handleExpand}
                        expandedKeys={this.state.expandedKeys}
                        checkedKeys={this.props.selectedKeys}
                    >
                        {
                            this.renderTreeNodes(treeData)
                        }
                    </DirectoryTree >
                    <div> 共{totalForm}张 </div>
                </Fragment> 
            )
        }else{
            return null
        }
    }
}
