import React, { Component } from 'react'
import styles from './index.module.less'
import {Card,Button,message} from 'antd'
import Left from './left'
import Right from './right'
import {withRouter} from 'react-router-dom';
import { postAction } from '@/axios';
class Step2 extends Component {
    state={
        treeData:[
            // { type:"folder", id:"000", name:"wenjianja", children:[
            //         { type:'form', id:"901", name:"biaodan" },
            //         { type:'folder', id:"008", name:"文件夹二" }
            //     ]
            // }
        ],
        selectedKeys:[],
        halfCheckedKeys:[],
        checkedNodes:[],
        planTaskId:"",
    }
    componentDidMount(){
        let fillPlanInfo = JSON.parse(window.localStorage.getItem("fillPlanInfo"));
        this.getTreeDataRequest();
        this.setState({
            planTaskId:fillPlanInfo.planTaskId,
        });
        this.getInitDataRequest(fillPlanInfo.planTaskId);
    }
    transFormData = (data) => data.map(item => {
        if(item.formId){//表单
            return ({
                type:"form",
                value:item.formId,
                name:item.formName,
                children:[]
            });
        }else{//文件夹
            let children = [...item.childrenFolder,...item.childrenForm];
            return({
                type:"folder",
                value:item.formFolderId,
                name:item.folderName,
                children:this.transFormData(children)
            }) 
        }
    })
    //获取树形目录
    getTreeDataRequest = () => {
        postAction("/ybt/planTask/getPlanFormTree").then(res => {
            // res.obj = [//测试数据
            //     { formFolderId: "1", folderName: "我的文件夹",
            //         childrenFolder: [
            //             { formFolderId: "22", folderName: "我的文件夹2", childrenFolder:[], childrenForm:[], }
            //         ],
            //         childrenForm:[
            //             { formId: "2", formName: "申报表", }
            //         ]
            //     }
            // ]
            if(res.success){
                this.setState({
                    treeData:this.transFormData(res.obj)
                })
            }else{
                message.error(res.obj);
            }
        })
    }
    //编辑状态下获取初始数据---请求
    getInitDataRequest = (planTaskId) => {
        postAction("/ybt/planTask/getPlanForm",{planTaskId}).then(res => {
            if(res.success){
                this.setState({
                    selectedKeys:res.obj.map(item => item.formId),
                    checkedNodes:res.obj.map(item => ({type:"form",...item}))
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    // 处理左侧树的选中事件
    handleCheckTree=(selectedKeys,halfCheckedKeys,checkedNodes)=>{
        this.setState({
            selectedKeys,
            halfCheckedKeys,
            checkedNodes
        })
    }
    //
    _preStep =() => {
        // const planInfo = JSON.stringify({type:"edit",...record});
        // window.localStorage.setItem("fillPlanInfo",planInfo)
        this.props.history.push('/fillPlan/step1')
    }
    //下一步
    _nextStep = () => {
        let params = {
            planTaskId:this.state.planTaskId,
            forms:this.state.checkedNodes.filter(
                item => item.type==="form"
            ).map(item=>({formId:item.formId,formName:item.formName}))
        }
        if(params.forms.length===0){
            message.error("请至少选择一个需要填报的表单");
            return false;
        }
        postAction("/ybt/planTask/createPlanForm",params).then(res => {
            if(res.success){
                this.props.history.push('/fillPlan/step3')
            }else{
                message.error(res.obj)
            }
        })
    }
    //
    _cancle = () => {
        // window.localStorage.setItem("fill")
        this.props.history.push('/fillPlan')
    }
    _deleteForm = (formId) => {
        // let selectedKeys = this.state.selectedKeys.filter(item=>item!==formId);
        let checkedNodes = this.state.checkedNodes.filter(item=>item.formId!==formId);
        let selectedKeys = checkedNodes.filter(item => item.type==="form").map(item=>item.formId);
        this.setState({
            selectedKeys,
            checkedNodes
        })
    }
    render() {
        let {treeData,selectedKeys}=this.state
        console.log(selectedKeys);
        console.log(this.state.checkedNodes)
        return (
            <div className={styles.main}>
                <Card title='新建填报计划' bodyStyle={{padding:'0 24px 24px 24px'}}>
                    <div className={styles.container}>
                        <div className={styles.layout}>
                            <Left selectedKeys={selectedKeys} treeData={treeData} handleCheckTree={this.handleCheckTree}/>
                        </div>
                        <div className={styles.layout}>
                            <Right deleteForm={this._deleteForm} checkedNodes={this.state.checkedNodes}/>
                        </div>
                    </div>
                </Card>
                <div className={styles.btns}>
                    <Button onClick={this._preStep} ghost type='primary' style={{marginRight:8}}>上一步</Button>
                    <Button onClick={this._nextStep} type='primary' style={{marginRight:8}}>下一步</Button>
                    <Button onClick={this._cancle}>取消</Button>
                </div>
            </div>
        )
    }
}
export default withRouter(Step2)